.button {
  padding: 7px 20px;
  margin: 8px;
  font-size: 18px;
  border-radius: 16px;
  border: none;
  color: white;
  cursor: pointer;
  box-shadow: 0 2px 6px rgba(133, 132, 132, 0.596);
  background-color: rgba(2, 2, 136, 0.8);
}

button:hover {
  background-color: rgb(2, 2, 136);
}
